* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}



/* navbar section */
nav {
  position: fixed;
  width: 100%;
  height: 12vh;
  background-color: rgb(225, 236, 236);
  display: flex;
  align-items: center;
  justify-content: space-around;
  box-shadow: 5px 5px 5px rgb(0, 0, 0, 0.1);
  z-index: 2;
}

ul {
  display: flex;
}

ul li {
  list-style: none;
  margin: 35px 35px;
  font-family: sans-serif;
}

nav ul li a {
  text-decoration: none;
  color: rgb(0, 0, 0, 0.5);
  font-size: 16px;
}

nav ul li a:hover {
  color: black;
}

.logo h1 {
  font-family: sans-serif;
}

.btn button {
  width: 80px;
  height: 30px;
  border: 1px solid black;
  font-family: sans-serif;
  font-size: 13px;
  padding: 5px;
  border-radius: 5px;
  cursor: pointer;
  margin: 8px 8px;
}

.btn button:hover {
  background-color: black;
  color: white;
}

/* Home section */

.background-image img {
  /* position: relative; */
  width: 100%;
  height: 100vh;
  object-fit: cover;
}

.text {
  position: absolute;
  top: 8%;
  left: 0%;
  width: 100%;
  height: 92%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  padding: 100px;
}

.text h1 {
  color: white;
  font-size: 50px;
  margin: 10px 10px;
  font-family: sans-serif;
}

.text p {
  color: white;
  font-size: 30px;
  margin: 10px 10px;
  font-family: sans-serif;
}

/* product section */

.container {
  width: 100%;
  height: 100vh;
  text-align: center;
  padding: 80px;
}

.container h1 {
  font-size: 45px;
  font-family: sans-serif;
}

.buttons {
  width: 100%;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;

  margin: 1%;

}

.buttons button {
  width: 135px;
  height: 35px;
  border: 1px solid black;
  font-family: sans-serif;
  font-size: 15px;
  border-radius: 5px;
  cursor: pointer;
  margin: 8px 8px;
  
}

.buttons button:hover {
  color: white;
  background-color: black;
}

.row-col {
  width: 100%;
  height: auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 50px;
  grid-row-gap: 50px;

}

.products-div {
  width: 80%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  box-shadow: 3px 3px 3px rgb(0, 0, 0, 0.1);
  padding: 20px;
  margin: 5%;
}

.products-div img {
  width: 400px;
  height: 200px;
  object-fit: contain;
  transition: all 0.5s;
}

.products-div img:hover {
  transform: scale(1.1);
  transition: all 0.5s;
}

.products-div h3 {
  font-family: sans-serif;
  margin: 1%;
}

.products-div p {
  font-weight: 600;
  margin: 5%;
  font-family: sans-serif;
}

.products-div button {
  width: 150px;
  height: 40px;
  border: 1px solid rgb(255, 255, 255);
  background-color: rgb(47, 47, 235);
  font-family: sans-serif;
  font-size: 13px;
  border-radius: 5px;
  cursor: pointer;
  margin: 5px 5px;
}

.products-div button a {
  text-decoration: none;
  color: rgb(255, 255, 255);
}


.loading {
  width: 330%;
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 50px;
  grid-row-gap: 50px;

}

/* product section */

.products-id {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.image {
  width: 100%;
  height: 100%;
}

.image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.content {

  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  font-family: sans-serif;
  padding: 50px;
}

.content h4 {
  font-size: 30px;
  text-transform: uppercase;
  color: rgb(0, 0, 0, 0.5);
  margin-bottom: 10px;
}

.content h1 {
  font-size: 35px;
  font-weight: 100;
  text-align: start;
  margin-bottom: 10px;
}

.rating {
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 50px;
}

.content h3 {
  font-size: 40px;
  margin-bottom: 25px;
}

.content p {
  font-size: 15px;
  color: rgb(0, 0, 0, 0.5);
  text-align: start;
  margin-bottom: 25px;
}

.id-buttons {
  width: 100%;
  display: flex;
  align-items: center;
}

.content button {
  width: 100px;
  height: 40px;
  border: 1px solid black;
  font-family: sans-serif;
  font-size: 13px;
  border-radius: 5px;
  cursor: pointer;
  margin: 5px 5px;
}

.content button:hover {
  background-color: black;
  color: white;
}

.id-buttons a {
  text-decoration: none;
  width: 100px;
  height: 40px;
  margin: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  color: rgb(255, 255, 255);
  font-family: sans-serif;
  border-radius: 5px;
  background: rgb(62, 59, 211);
}

/* cart section */
.Cart-container {
  width: 100%;
  height: 150vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.5);
}

.form h1 {
  font-family: sans-serif;
  margin: 2%;
}

.form {
  width:100%;
  height: 100%;
  box-shadow: 5px 5px 5px rgb(109, 100, 100, .5);
  display: flex;
  align-items: center;
  justify-content: center;

}

form {
  width: 80%;
  height: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

}

.Input {
  width: 100%;
  height: 7%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: rgb(0, 0, 0);
  font-family: sans-serif;
}

.lable {
  width: 30%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.input {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;

}

.input input {
  width: 100%;
  height: 70%;
  outline: none;
  padding: 20px;
  font-size: 18px;
  font-family: sans-serif;
}

form button {
  width: 150px;
  height: 40px;
  border: 1px solid black;
  font-family: sans-serif;
  font-size: 13px;
  border-radius: 5px;
  cursor: pointer;
  margin: 15px 5px;
  padding: 10px;
}

form button:hover {
  color: white;
  background-color: black;
}

/* ABOUT SECTION */

.about-us {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.about-image {
  width: 30%;
  height: 80%;

}

.about-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.about-content {
  width: 50%;
  height: 80%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
}

.about-content h1 {
  font-family: sans-serif;
  font-size: 35px;
  padding: 20px;
  margin: 1%;
  color: rgb(0, 0, 0, .5);
}

.about-content p {
  font-family: sans-serif;
  font-size: 17px;
  padding: 20px;
  margin: 1%;
}

/* contact section */

.contact {
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(rgba(0, 0, 0, .5), rgba(0, 0, 0, .5)), url("https://www.pixelstalk.net/wp-content/uploads/2016/10/Beautiful-Building-Wallpaper-HD.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.contact-form {
  width: 60%;
  height: 15%;
  background-color: rgb(255, 255, 255,.5);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
}

.contact-form form {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: row;

}

.contact-form form input {
  width: 30%;
  height: 45%;
  border-radius: 5px;
  border:none;
  outline: none;
  padding: 13px;
  font-family: sans-serif;
  font-size: 15px;
}

.contact h1 {
  height: 20%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-family: sans-serif;
  font-size: 35px;
  margin: 2%;
}

.cards {
  width: 100%;
  height: 30%;
  margin: 5%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.card {
  width: 25%;
  height: 60%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: rgb(121, 108, 85);
  border-radius: 10px;
  margin: 1%;
}

.card p {
  color: white;
  font-family: sans-serif;
  margin: 1%;
  text-align: center;
  font-size: 12px;
  padding: 5px;
}

.card h1 {
  font-size: 25px;
  margin: 5%;
}

.contact-form form button {
  background-color: rgb(121, 108, 85);
  color: white;
  border-radius: 10px;
  border: none;
}
